*{
  box-sizing: border-box;
}

html{
  font-size: 14px;
  font-family: 'Open Sans', sans-serif !important;
}

body{
  padding: 0;
  margin: 0;
}

html body button {
  user-select: none; }

html body img, html body svg {
  user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none; }

a{
  text-decoration: none;
  color: black;
  transition: 0.2s;
}


.fullpage{
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.fullpage > div{
  height: 100%;
  flex-grow: 3;
  padding-bottom: 70px;
}

.common_block{
  margin: 4.571rem 0;
}
.common_title{
  font-size: 1.7142rem;
  margin-bottom: 2.28rem;
  font-weight: 700;
}

.img-rect{
  position: relative;
  padding-top: 100%;
  background: #EAEAEA;
  width: 100%;
  overflow: hidden;
}
.img-rect img{
  top: 0; left: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.btn{
  user-select: none;
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  padding: 14px 28px;
  border-radius: 4px;
  color: white;
  border: none;
  box-shadow: none;
  font-weight: bold;
  font-size: 14px;
  text-align: center;
}

.btn-sm{
  padding: 11px 17px;
}

.btn-lg-w{
  max-width: 15.5rem;
  width: 100%;
}

.btn-dark{
  color: white;
  background: #434343;
}
.btn-dark:hover{
  color: white;
  background: #292929;
}

.btn-green{
  color: white;
  background: #37A77F;
}
.btn-green:hover{
  color: white;
  background: #2e8667;
}


.breadcrumb{
  padding: 13px 0;
  font-size: 1rem;
}

.breadcrumb.breadcrumb-gray, .breadcrumb.breadcrumb-gray a{
  color: #929292;
}
.breadcrumb.breadcrumb-gray a:hover{
  color: black;
}

.header-title{
  margin-top: 0;
  margin-bottom: 0;
  font-size: 2.857rem;
}

.header-title-1{
  margin-top: 5.7142rem;
}

.header-title-2{
  margin-top: 0;
}

.header-title-2 span{
  color: #CF9259;
}

/* Header */
.header_container{
  background: #FFFFFF;
  box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.15);
  padding: 0.9285714286rem 0;
  z-index: 1000;
}


/* Menu */
.menu{
  padding: 0;
  margin: 0;
  list-style: none;
}

.menu.header__menu{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-left: -2.4285714286rem;
}

.menu.header__menu li{
  text-transform: uppercase;
  margin-left: 2.4285714286rem;
}
.menu.header__menu li a{
  font-weight: 400;
  color: black;
  font-size: 1rem;
}


.menu__item.menu__item--catalog > a{
  display: flex;
  align-items: center;
  font-weight: 700 !important;
}
.menu__item.menu__item--catalog > a > svg{
  margin-right: 0.8571428571rem;
}

/* Search */
.search input, .search input:focus, .search input:active{
  padding: 0;
  margin: 0;
  background: transparent;
  outline: none;
  box-shadow: none;
  border: none;
}
.search.header__search{
  width: 100%;
  display: flex;
  align-self: center;
  background: #EDEDED;
  border-radius: 4px;
  padding: 0.85rem 0.85rem 0.85rem 1.142rem;
}
.search.header__search button{
  transition: 0.2s;
  cursor: pointer;
  margin: 0;
  margin-right: 0.57rem;
  padding: 0;
  border: none;
  outline: none;
  box-shadow: none;
  background: transparent;
}
.search.header__search button > svg{
  margin-bottom: -2px;
  width: inherit;
  height: inherit;
}
.search.header__search button:active, .search.header__search button:focus, .search.header__search button:hover{
  border: none;
  outline: none;
  box-shadow: none;
  background: transparent;
}
.search.header__search button:hover{
  transform: scale(1.1);
}
.search.header__search input{
  width: 100%;
}
.search.header__search input::placeholder{
  color: #929292;
}

/* Panel into header */
.header__panel{
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

/* Buttons wishlist and user into header */
.header__buttons{
  display: flex;
  align-items: center;
}

.header__buttons > .header__button{
  margin-left: 1.42rem;
}

.header__button.header__button--wishlist{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.8571428571rem;
  height: 2.8571428571rem;
  position: relative;
}
.header__button.header__button--wishlist > .counter{
  user-select: none;
  position: absolute;
  top: 0;
  right: 0;
  color: white;
  background: #434343;
  border-radius: 50%;
  width: 1.14rem;
  height: 1.14rem;
  font-size: 9px;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 1.1;
}
.header__button.header__button--user{
  background: #FFFFFF;
  border: 2px solid #EEEEEE;
  box-sizing: border-box;
  border-radius: 0.57rem;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.8571428571rem;
  height: 2.8571428571rem;
}

