/*Catalog filter*/

.catalog-container{
  margin-top: 32px;
}

.catalog-container .discard-filter{
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 100%;
  padding: 14px;
  border: 1px solid #EFEFEF;
  box-sizing: border-box;
  border-radius: 4px;
  color: black;
  font-size: 1rem;
  font-weight: 700;
  transition: 0.2s;
  margin-bottom: 8px;
}

.catalog-container .discard-filter svg{
  margin-right: 8px;
}

.catalog-container .discard-filter:active{
  background-color: #f2f2f2;
}


/*Catalog filter*/
.filter-parent{
  margin-bottom: 8px;
}
.filter-parent .filter-parent-title{
  user-select: none;
  cursor: pointer;
  padding: 1rem 1.428rem;
  font-weight: bold;
  font-size: 1rem;
  color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  border-radius: 4px;
  transition: 0.2s;
}
.filter-parent .filter-parent-title > svg{
  transition: 0.2s;
  transform: rotate(180deg);
}

.filter-parent.open .filter-parent-title{
  background: #F8F8F8;
}

.filter-parent.open .filter-parent-title > svg{
  transform: rotate(0deg);
}

.filter-parent .filter-parent-title:hover{
  background: #f9f9f9;
}

.filter-parent.open  .filter-parent-title:hover{
  background: #f1f1f1;
}

.filter-parent .filter-child{
  display: none;
  padding: 12px 24px;
  margin-bottom: -12px;
}

.filter-parent .filter-child .filter-item{
  user-select: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}
.filter-parent .filter-child .filter-item .checkbox{
  transition: 0.2;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 2px;
  width: 16px;
  height: 16px;
  border: 1px solid #E0E0E0;
  margin-right: 8px;
}
.filter-parent .filter-child .filter-item .checkbox svg{
  transition: 0.2s;
  opacity: 0;
  margin-bottom: -1px;
    margin-right: -1px;
}
.filter-parent .filter-child .filter-item:hover .checkbox{
  background: #f8f8f8;
}

.filter-parent.open .filter-child{
  display: block;
}

.filter-parent .filter-child .filter-item.selected .checkbox{
  background: #CF9259;
  border: 1px solid #CF9259;
}

.filter-parent .filter-child .filter-item.selected .checkbox svg{
  opacity: 1;
}



